.crm-header {
    width: 100%;
    margin-left: 36px;
    padding-top: 5px;
    position: relative;
    z-index: 333;
    top: -5px;
    padding-right: 20px;

    background-color: white;
    white-space: nowrap;
    > div {
        display: flex;

        flex-wrap: nowrap;
    }
    .crm-header-link.btn-toggle {
        font-weight: 400;
        position: relative;
        cursor: default;
        .color-form {
            height: 4px;
            bottom: 2px;
        }
    }
    .crm-header-link.disable {
        opacity: 0.3;
        cursor: default;
    }

    .crm-header-link {
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 5px;
        padding-bottom: 5px;
        // white-space: nowrap;
        box-sizing: border-box;
        position: relative;
        text-align: center;
        margin: 1px;
        cursor: pointer;
        font-size: 12px;
        user-select: none;

        &:hover:not(.disable, .btn-toggle) .color-form,
        &:hover:not(.disable, .btn-toggle) .btn-link {
            opacity: 0.5;
        }
        .new-orders-header {
            position: relative;
            &::after {
                content: "";
                width: 4px;
                height: 4px;
                border-radius: 100%;
                background-color: #00b9ff;
                position: absolute;
                left: -7px;
                top: 5px;
            }
        }
        .count-link {
            font-size: 10px;
            opacity: 0.5;
        }
    }
}

.scroll-wrap {
    height: 95%;
    .simplebar-track.simplebar-vertical {
        background-color: rgba(0, 0, 0, 0);
        width: 8px;
        margin-right: 0;
        margin-top: 0;
        margin-bottom: 40px;
        cursor: pointer;
    }
    .simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
        background: rgba(0, 0, 0, 0.3);
        width: 8px;
        border-radius: 5px;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        cursor: pointer;
    }
    .simplebar-track.simplebar-horizontal {
        background-color: #0000;
        height: 8px;
        margin-right: 10px;
        margin-bottom: 0;
        margin-left: 20px;
        cursor: pointer;
        padding-top: 2px;
        bottom: 0;
        border-radius: 10px;
        // top: -2px;
    }
    .simplebar-track.simplebar-horizontal .simplebar-scrollbar::before {
        background: rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        height: 8px;
        left: 0;
        right: 0;
        bottom: 8px;
        top: 0px;
        cursor: pointer;
        margin-bottom: 2px;
    }
    .simplebar-scrollbar.simplebar-visible:before {
        opacity: 1;
        cursor: pointer;
    }
    .simplebar-track.simplebar-horizontal .simplebar-scrollbar {
        height: 10px;
    }
    .simplebar-content-wrapper {
        padding-right: 15px;
        padding-left: 15px;
        padding-bottom: 30px;
    }
}

@media screen and (max-height: 800px) {
    .scroll-wrap {
        .simplebar-track.simplebar-horizontal {
            margin-bottom: 30px;
        }
    }
}

.scroll-wrap2 {
