            text-align: center;
            right: -3px;
            padding: 0px 2.5px;
            line-height: 10px;
            top: -5px;
        }
    }
    .product-colum {
        text-align: left;
        line-height: 18px;
        pointer-events: all;
        .max-lenght-product {
            margin-right: 45px;
        }
        .ico-wrap {
            position: absolute;
            top: 4px;
            right: 32px;
            .count {
                top: -3px;
                right: -5px;
                font-family: "Roboto";
            }
            .icons {
                position: relative;
                padding-right: 7px;
                top: -1px;
                left: -1px;
                font-size: 12px;
            }
        }
        .ico-wrap:last-child {
            right: 8px;
            top: 4px;
            .icons {
                padding-right: 7px;
                left: 0;
                top: 0;
                font-size: 12px;
            }
            .count {
                right: -6px;
                top: -3px;
            }
        }
    }
    .colum-pay {
        text-align: center;
        font-size: 16px;
    }
    .colum-delivery {
        text-align: center;

        font-size: 16px;
    }
    .prro-colum {
        .prro-number {
            margin-right: 22px;
        }
        .icons {
            position: absolute;
            top: 3px;
            right: 4px;
        }
    }
    .colum-ttn {
        min-width: 116px;
        .ttn-position {
            height: 18px;
            line-height: 18px;
            .ico-wrap {
                margin: 0;
                position: absolute;
                top: 4px;
                right: 8px;
                .count {
                    top: -3px;
                    right: -5px;
                    font-family: "Roboto";
                }
                .icons {
                    position: relative;
                    top: -1px;
                    left: -1px;
                    font-size: 12px;
                }
            }
            .ttn-number {
                display: flex;
                position: relative;
                .ttn-first {
                    position: relative;
                    width: 42px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: inline-block;
                    transition: 0.2s;
                    padding-right: 4.5px;
                }
                .icon-Vector-81 {
                    position: relative;
                    left: 0;
                    top: 3px;
                    &:after {
                        content: "\7a";
                        transform: rotate(-90deg);
                        position: absolute;
                        font-size: 6px;
                        opacity: 0.6;
                        left: 0;
                        top: -1px;
                    }
                    &:before {
                        transform: rotate(90deg);
                        position: absolute;
                        font-size: 6px;
                        opacity: 0.6;
                        right: 0;
                        top: -1px;
                    }
                }
                .trigger-mouse-2 {
                    position: absolute;
                    width: 0px;
                    height: 16px;
                    right: 0px;
                    transition: 0.2s;
                    background-color: #0000;
                }
                .trigger-mouse {
                    position: absolute;
                    width: 0px;
                    height: 16px;
                    z-index: 33;
                    transition: 0.2s;
                    left: 0;
                    background-color: #0000;
                }
                .ttn-second {
                    width: 42px;
                    direction: rtl;
                    display: inline-block;
                    padding-left: 4.5px;
                    position: relative;
                    left: 0;
                    transition: 0.2s;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .hide-arrow-ttn1:before {
                    opacity: 0;
                    transition: 0.2s;
                }
                .hide-arrow-ttn2:after {
                    opacity: 0;
                    transition: 0.2s;
                }
            }
        }
    }
}

